"use client";

import React from 'react';
import {ChangeEvent, useState} from 'react';
import { useRouter, useSearchParams } from 'next/navigation';


import { Save, X } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { DateTimePicker } from '@/components/ui/datetimepicker';
import { showToast } from '@/lib/utils/toast-helper';

import { useIndexedDB } from "@/lib/hooks/useIndexedDB";
import { DBConfig } from "@/lib/data/dbconfig";

import { Invoice } from '@/lib/models/invoices';

import { createQueryString } from '@/lib/utils/utils';

import { 
    selectFile,
    selectFileName,
    selectData,
 } from '@/lib/features/document_ocr/document.slice'

import { useAppDispatch, useAppSelector } from "@/lib/hooks/hooks";
import moment from 'moment';

export function InvoiceLayout() {

    let router = useRouter();
    const searchParams = useSearchParams();

    const {
        putValue,
        getValue,
        getAllValue,
        updateValue,
        deleteValue,
        isDBConnecting,
      } = useIndexedDB(DBConfig.name, [DBConfig.invoiceTable]);

    const uploadedFile = useAppSelector(selectFile);
    const fileName = useAppSelector(selectFileName);
    
    if (searchParams.get("id")) {
        showToast("success", "Data has been saved!");
    }
    const data = useAppSelector(selectData);
    

    const [formData, setFormData] = useState({
        filename: fileName,
        type: data.type,
        vendor: data.vendor,
        place: data.place,
        item: data.item,
        item_type: data.item_type,
        qty: data.qty,
        date: moment(data.date, "DD/MM/YYYY hh:mm:ss").toDate(),
        checkin_time: moment(data.checkin_time, "DD/MM/YYYY hh:mm:ss").toDate(),
        checkout_time: moment(data.checkout_time, "DD/MM/YYYY hh:mm:ss").toDate(),
        amount: data.amount
      });

    const handleInputChange = (e: any) => {
        const { name, value } = e.target;
        setFormData((prevFormData) => ({
            ...prevFormData,
            [name]: value,
        }));
    };

    const setNewDate = (newDate: any) => {
        // setDate(newDate);
        setFormData({ ...formData, ["date"]: newDate });
      }

    const setNewCheckinTime = (newCheckinTime: any) => {
        // setCheckinTime(newCheckinTime);
        setFormData({ ...formData, ["checkin_time"]: newCheckinTime });
    }

    const setNewCheckoutTime = (newCheckoutTime: any) => {
        // setCheckoutTime(newCheckoutTime);
        setFormData({ ...formData, ["checkout_time"]: newCheckoutTime });
    }


    const handleSubmit = (e: ChangeEvent<HTMLFormElement>) => {
        e.preventDefault(); // Prevent default form submission behavior
        // if (isEditing !== null) {
        //   updateValue({
        //     tableName: Database.userTable,
        //     id: isEditing,
        //     newItem: formData,
        //   });
        //   setIsEditing(null);
        // } else {
        //   putValue(Database.userTable, formData);
        // }
        // setFormData({ firstname: "", lastname: "" });
        // getLatestValues(); // Refresh the list after submission

        putValue(DBConfig.invoiceTable, formData).then((value) => {
            console.log("SAVE >> ", value);            
            router.push("/invoice_listing" + "?" + createQueryString("status", "success"));
        });
      };

    return(
        <div>
            {
                (() => {
                    if(data.type == "receipt") {
                            return (
                                <b>Receipt</b>
                            )
                        } else {
                            return (
                                <b>Undefined</b>
                            )
                        }
                })()  
            }  is found in the image.

            <form className="flex items-center w-full justify-between" onSubmit={handleSubmit}>
                <div className="grid grid-flow-col gap-4 mt-3">
                    <div>

                        <div className="mt-2">
                            <div className="flex flex-row items-center">
                                <label className="basis-2/5">Vendor :</label>
                                <Input type="text" 
                                    name="vendor"
                                    onChange={handleInputChange}
                                    value={formData.vendor}
                                    className="basis-2/5 text-gray-500 font-medium text-sm bg-gray-100 file:cursor-pointer cursor-pointer file:border-0 file:py-2 file:px-4 file:mr-4 file:bg-gray-800 file:hover:bg-gray-700 file:text-white rounded"/>
                            </div>
                        </div>

                        <div className="mt-2">
                            <div className="flex flex-row items-center">
                                <label className="basis-2/5">Place :</label>
                                <Input type="text" 
                                    name="place"
                                    onChange={handleInputChange}
                                    value={formData.place}
                                    className="basis-2/5 text-gray-500 font-medium text-sm bg-gray-100 file:cursor-pointer cursor-pointer file:border-0 file:py-2 file:px-4 file:mr-4 file:bg-gray-800 file:hover:bg-gray-700 file:text-white rounded"/>
                            </div>
                        </div>
                        
                        <div className="mt-2">
                            <div className="flex flex-row items-center">
                                <label className="basis-2/5">Item :</label>
                                <Input type="text" 
                                    name="item"
                                    onChange={handleInputChange}
                                    value={formData.item}
                                    className="basis-2/5 text-gray-500 font-medium text-sm bg-gray-100 file:cursor-pointer cursor-pointer file:border-0 file:py-2 file:px-4 file:mr-4 file:bg-gray-800 file:hover:bg-gray-700 file:text-white rounded"/>
                            </div>
                        </div>
                        
                        <div className="mt-2">
                            <div className="flex flex-row items-center">
                                <label className="basis-2/5">Item Type :</label>
                                <Input type="text" 
                                    name="item_type"
                                    onChange={handleInputChange}
                                    value={formData.item_type}
                                    className="basis-2/5 text-gray-500 font-medium text-sm bg-gray-100 file:cursor-pointer cursor-pointer file:border-0 file:py-2 file:px-4 file:mr-4 file:bg-gray-800 file:hover:bg-gray-700 file:text-white rounded"/>
                            </div>
                        </div>
                        
                        <div className="mt-2">
                            <div className="flex flex-row items-center">
                                <label className="basis-2/5">Qty :</label>
                                <Input type="text" 
                                    name="qty"
                                    onChange={handleInputChange}
                                    value={formData.qty}
                                    className="basis-2/5 text-gray-500 font-medium text-sm bg-gray-100 file:cursor-pointer cursor-pointer file:border-0 file:py-2 file:px-4 file:mr-4 file:bg-gray-800 file:hover:bg-gray-700 file:text-white rounded"/>
                            </div>
                        </div>
                        
                        <div className="mt-2">
                            <div className="flex flex-row items-center">
                                <label className="basis-2/5">Date :</label>
                                <DateTimePicker size={35} value={formData.date} timeDisplay={false} onChange={setNewDate}/>
                            </div>
                        </div>

                        <div className="mt-2">
                            <div className="flex flex-row items-center">
                                <label className="basis-2/5">Check-in Time :</label>
                                <DateTimePicker size={35} value={formData.checkin_time} timeDisplay={true} onChange={setNewCheckinTime}/>
                            </div>
                        </div>

                        <div className="mt-2">
                            <div className="flex flex-row items-center">
                                <label className="basis-2/5">Check-out Time :</label>
                                <DateTimePicker size={35} value={formData.checkout_time} timeDisplay={true} onChange={setNewCheckoutTime}/>
                            </div>
                        </div>

                        <div className="mt-2">
                            <div className="flex flex-row items-center">
                                <label className="basis-2/5">Amount :</label>
                                <Input type="text" 
                                    name="amount"
                                    onChange={handleInputChange}
                                    value={formData.amount}
                                    className="basis-2/5 text-gray-500 font-medium text-sm bg-gray-100 file:cursor-pointer cursor-pointer file:border-0 file:py-2 file:px-4 file:mr-4 file:bg-gray-800 file:hover:bg-gray-700 file:text-white rounded"/>
                            </div>
                        </div>

                        <div className="mt-6 pl-80">
                            <div className="flex flex-row items-center">
                                <Button type='submit' size="sm" variant="default" className="h-8 gap-1">
                                    <Save className="h-4 w-4" />
                                    <span className="sr-only sm:not-sr-only sm:whitespace-nowrap">
                                        Save
                                    </span>
                                </Button>
                                
                                <Button size="sm" variant="outline" className="h-8 gap-1 ml-2">
                                    <X className="h-4 w-4" />
                                    <span className="sr-only sm:not-sr-only sm:whitespace-nowrap">
                                        Cancel
                                    </span>
                                </Button>
                            </div>
                        </div>
                    </div>
                    <div className='ml-1'><h5>{fileName}</h5><img src={uploadedFile} className='w-3/6 h-fill' /></div>
                </div>
            </form>
        </div>
    )

    
}